.checkout{
    width:100vw;
    height:90vh;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.checkout-container{
    height: 40%;
    width: 70%;
    display:grid;
    grid-template-columns: repeat(2,fr);
    -ms-flex-align: center;
        align-items: center;
    margin: 0 auto;
    /* flex-direction: column; */
    /* justify-content: space-around; */
    /* align-items: center; */
    
}

.mapped-shipping{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    
}

.mapped-shipping > img{
    width: 5%;
}

.showFirstNameEdit{
    height:0px;
    background: yellow;
    overflow: hidden; 
}

.showFirstName{
    height: 100px
}

.ele-div{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 20%;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    margin-top:7px;
}

.ele-div > img{
    width: 5%;
    
}

.ele-div > input{
    -webkit-box-shadow: 5px 5px 3px #888888;
            box-shadow: 5px 5px 3px #888888;
}

.checkout-bitcoin{
    width: 35%;
    height: 40%;
    border-radius:7%;
    background:#E9EBF0
}

.totals{
    width: 100%;
    height: 10%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.checkout-buttons-div{
    height: 20%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}




